import React from "react";
import {Link} from "react-router-dom";


class Login extends React.Component{

    constructor(props) {
        super(props);
        this.handleOnChangeLogin = this.handleOnChangeLogin.bind(this);
        this.handleOnChangePassword = this.handleOnChangePassword.bind(this);
        this.handleOnSubmit = this.handleOnSubmit.bind(this);
        this.state = {
            login : '',
            password : ''
        }

    }

    componentDidMount() {
        fetch("http://localhost:8080/access?mode=login",{
            headers : {
                'Authorization' : localStorage.getItem("token")
            }
        })
            .then((response) => response.json())
            .then((result) => {
                if (result.error == "YES")
                    this.props.history.push("/main");
            })

    }


    handleOnChangeLogin(event) {
        this.setState({login : event.target.value});
    }

    handleOnChangePassword(event) {
        this.setState({password : event.target.value})
    }

    handleOnSubmit(event) {
            event.preventDefault();
            if (this.state.login && this.state.password){
                const user = {
                    nickname: this.state.login,
                    password: this.state.password
                }
                fetch("http://localhost:8080/login", {
                    method : 'POST',
                    headers : {
                        'Content-Type': 'application/json'
                    },
                    body : JSON.stringify(user)
                })
                    .then((response) => {
                        if (!response.ok)
                            throw new Error();
                        response.json()
                            .then((result) => {
                                if (result.error == "NO"){
                                    localStorage.setItem("token", "Basic " + result.message);
                                    this.props.history.push("/main");
                                }else{
                                    document.getElementById("Message").innerHTML = result.message;
                                }
                            })
                    })
                    .catch((e) => {
                        document.getElementById("Message").innerHTML = "Неверный пароль.";
                    })
            }else{
                document.getElementById("Message").innerHTML = "Заполните поля."
            }

    }

    render() {
        return(
            <div>
                <form onSubmit={this.handleOnSubmit}>
                    <p>Вход</p>
                    <div>
                        <label>Логин</label>
                        <br/>
                        <input onChange={this.handleOnChangeLogin} type="text"/>
                        <br/>
                        <label>Пароль</label>
                        <br/>
                        <input onChange={this.handleOnChangePassword} type="password"/>
                        <br/>
                        <input  type="submit"/>
                    </div>
                </form>
                <div id="Message"/>
                <div>
                    <Link to='/registration'>Регистрация</Link>
                </div>
            </div>

        );
    }

}

export default Login;