import React from "react";
import {Link} from "react-router-dom";

class Registration extends React.Component{

    constructor(props) {
        super(props);
        this.handleOnChangeLogin = this.handleOnChangeLogin.bind(this);
        this.handleOnChangePassword = this.handleOnChangePassword.bind(this);
        this.handleOnSubmit = this.handleOnSubmit.bind(this);
        this.state = {
            login : '',
            password : ''
        }
    }


    componentDidMount() {
        fetch("http://localhost:8080/access?mode=registration",{
            headers : {
                'Authorization' : localStorage.getItem("token")
            }
        })
            .then((response) => response.json())
            .then((result) => {
                if (result.error == "YES")
                    this.props.history.push("/main");
            })

    }




    handleOnChangeLogin(event) {
        this.setState({login : event.target.value});
    }

    handleOnChangePassword(event) {
        this.setState({password : event.target.value})
    }

    handleOnSubmit(event) {
        event.preventDefault();
        if (this.state.login && this.state.password ){
            const user = {
                nickname: this.state.login,
                password: this.state.password
            }
            fetch("http://localhost:8080/users", {
                method : 'POST',
                headers : {
                    'Content-Type': 'application/json'
                },
                body : JSON.stringify(user)
            }).then((response) => response.json())
                .then((result) => {
                    document.getElementById("Message").innerHTML = result.message;
                });
        }else{
            document.getElementById("Message").innerHTML = "Заполните поля."
        }

    }



    render() {
        return(
            <div>
                <form onSubmit={this.handleOnSubmit}>
                    <p>Регистрация</p>
                    <div>
                        <label>Логин</label>
                        <br/>
                        <input onChange={this.handleOnChangeLogin} type="text"/>
                        <br/>
                        <label>Пароль</label>
                        <br/>
                        <input onChange={this.handleOnChangePassword} type="text"/>
                        <br/>
                        <input  type="submit"/>
                    </div>
                </form>
                <div id="Message"/>
                <div>
                    <Link to='/login'>Вход</Link>
                </div>
            </div>
        )
    }
}

export default Registration;