import React from "react";
import {Link} from "react-router-dom";
import area from "../pictures/areas.png";


class Main extends React.Component{

    constructor(props) {
        super(props);
        this.handleOnSubmit = this.handleOnSubmit.bind(this);
        this.handleOnChangeY = this.handleOnChangeY.bind(this);
        this.logout = this.logout.bind(this);
        this.NextPage = this.NextPage.bind(this);
        this.PrevPage = this.PrevPage.bind(this);
        this.Refresh = this.Refresh.bind(this);
        this.state = {
            x : '-4',
            y : '',
            r : '-4',
            massive : [],
            page : 0
        }

    }





    componentDidMount() {
        fetch("http://localhost:8080/access?mode=main",{
            headers : {
                'Authorization' : localStorage.getItem("token")
            }
        })
            .then((response) => response.json())
            .then((result) => {
                if (result.error == "YES")
                    this.props.history.push("/login");
                else {
                    fetch("http://localhost:8080/points?page=" + this.state.page, {
                        headers : {
                            'Authorization' : localStorage.getItem("token")
                        }
                    })
                        .then(results => {return results.json()})
                        .then(data => this.setState({massive : data}))
                }
            })

    }

    handleOnChangeRadioX(event){
        this.setState({x : event.target.value});
    }

    handleOnChangeRadioR(event){
        this.setState({r : event.target.value});
    }

    handleOnChangeY(event){
        this.setState({y : event.target.value});
    }

    addRow(result){
        let body = document.getElementById("table");
        let row = document.createElement("tr");
        let td1 = document.createElement("td");
        td1.appendChild(document.createTextNode(this.state.x));
        let td2 = document.createElement("td");
        td2.appendChild(document.createTextNode(this.state.y));
        let td3 = document.createElement("td");
        td3.appendChild(document.createTextNode(this.state.r));
        let td4 = document.createElement("td");
        td4.appendChild(document.createTextNode(result.result));
        row.appendChild(td1);
        row.appendChild(td2);
        row.appendChild(td3);
        row.appendChild(td4);
        body.appendChild(row);
    }

    //Нужно переделать
    handleOnSubmit(event){
        document.getElementById("Message").innerHTML = "";
        event.preventDefault();
        if (this.state.y){
            const point = {
                x : this.state.x,
                y : this.state.y,
                r : this.state.r
            }
            fetch("http://localhost:8080/points",{
                method : 'POST',
                headers : {
                    'Content-Type' : 'application/json',
                    'Authorization' : localStorage.getItem("token")
                },
                body : JSON.stringify(point)
            })
                .then((response) => {
                    if (!response.ok)
                        throw new Error()
                    response.json()
                        .then((result) => {
                            if (result.error == 'YES') {
                                document.getElementById("Message").innerHTML = result.result;
                                }else{
                                    this.Refresh()
                                }
                        })
                })
                .catch((e) => console.log(e));
        }else{
            document.getElementById("Message").innerHTML = "Заполните поле для координаты Y";
        }

    }

    NextPage(event){
        this.setState({page : this.state.page + 1})
        fetch("http://localhost:8080/points?page=" + Number(this.state.page + 1), {
            headers : {
                'Authorization' : localStorage.getItem("token")
            }
        })
            .then(results => {return results.json()})
            .then(data => this.setState({massive : data}))
    }

    Refresh(event){
        fetch("http://localhost:8080/points?page=" + Number(this.state.page) , {
            headers : {
                'Authorization' : localStorage.getItem("token")
            }
        })
            .then(results => {return results.json()})
            .then(data => this.setState({massive : data}))
    }

    PrevPage(event) {
        event.preventDefault();
        this.setState({page : this.state.page - 1})
        fetch("http://localhost:8080/points?page=" + Number(this.state.page - 1), {
            headers : {
                'Authorization' : localStorage.getItem("token")
            }
        })
            .then(results => {return results.json()})
            .then(data => this.setState({massive : data}))

    }

    logout(event){
        event.preventDefault();
        fetch("http://localhost:8080/exit",{
            method : 'POST',
            headers : {
                'Authorization' : localStorage.getItem("token")
            }
        })
            .then((response) => response.json())
            .then((result) => {
                if (result.error == 'NO'){
                    localStorage.removeItem("token");
                    this.props.history.push("/login");
                }
            })
            .catch((e) => alert(e))
    }


    render() {
        return(
            <div>
                <img src={area} height="200" width="200"/>
                <form onSubmit={this.handleOnSubmit}>
                    <p>Координата X</p>
                    <div id="RadioX" onChange={this.handleOnChangeRadioX.bind(this)}>
                        <label>-4</label>
                        <input type="radio" name="x" value="-4" defaultChecked={true}/>

                        <label>-3</label>
                        <input type="radio" name="x" value="-3"/>

                        <label>-2</label>
                        <input type="radio" name="x" value="-2"/>

                        <label>-1</label>
                        <input type="radio" name="x" value="-1"/>

                        <label>0</label>
                        <input type="radio" name="x" value="0"/>

                        <label>1</label>
                        <input type="radio" name="x" value="1"/>

                        <label>2</label>
                        <input type="radio" name="x" value="2"/>

                        <label>3</label>
                        <input type="radio" name="x" value="3"/>

                        <label>4</label>
                        <input type="radio" name="x" value="4"/>
                    </div>
                    <p>Координата Y</p>
                    {/*Валидация*/}
                    <input onChange={this.handleOnChangeY} type="text"/>

                    <p>Параметр R</p>
                    <div id="RadioR" onChange={this.handleOnChangeRadioR.bind(this)}>
                        <label>-4</label>
                        <input type="radio" name="r" value="-4" defaultChecked={true}/>

                        <label>-3</label>
                        <input type="radio" name="r" value="-3"/>

                        <label>-2</label>
                        <input type="radio" name="r" value="-2"/>

                        <label>-1</label>
                        <input type="radio" name="r" value="-1"/>

                        <label>0</label>
                        <input type="radio" name="r" value="0"/>

                        <label>1</label>
                        <input type="radio" name="r" value="1"/>

                        <label>2</label>
                        <input type="radio" name="r" value="2"/>

                        <label>3</label>
                        <input type="radio" name="r" value="3"/>

                        <label>4</label>
                        <input type="radio" name="r" value="4"/>
                    </div>
                    <br />
                    <input type="submit"/>
                    <br />
                    <button onClick={this.logout}>Выйти</button>
                </form>
                <div id="Message" />

                <table id="table" >
                    <thead>
                        <tr>
                            <th>X</th>
                            <th>Y</th>
                            <th>R</th>
                            <th>Результат</th>
                        </tr>
                    </thead>
                    <tbody>
                    {
                        this.state.massive.map(item => {
                            return(
                                <tr key={item.id}>
                                <td>{item.x}</td>
                                <td>{item.y}</td>
                                <td>{item.r}</td>
                                <td>{item.result}</td>
                                </tr>
                            );

                        })
                    }
                    </tbody>
                </table>
                <button onClick={this.PrevPage}>Предыдущая страница</button>
                <button onClick={this.NextPage}>Следующая страница</button>
                <p>{this.state.page}</p>

            </div>
        );
    }
}

export default Main;